<template>
  <el-card class="system-card">
    <!--头部导航+搜索-->
    <div class="system-nav-bar" ref="navBarHeight">
      <div class="left">
        <div :class="remindType===0 ? 'all cur':'all'" @click="tabHandle(0)">全部({{allNumber?allNumber:0}})</div>
        <div :class="remindType===-1 ?'unread cur':'unread'" @click="tabHandle(-1)">未读({{noReadNumber?noReadNumber:0}})</div>
        <span class="line-center">|</span>
        <div :class="remindType===1 ?'done cur':'done'" @click="tabHandle(1)">
          待办消息({{remindCount.upcoming}})
        </div>
        <div :class="remindType===2 ?'expire cur':'expire'" @click="tabHandle(2)">
          到期消息({{remindCount.maturity}})
        </div>
<!--        <div :class="remindType===3 ?'warn cur':'warn'" @click="tabHandle(3)">-->
<!--          提醒消息({{remindCount.remind}})-->
<!--        </div>-->
        <span class="line-center">|</span>
        <div @click="readAllHandle">全部标为已读</div>
      </div>
      <div class="right">
        <el-date-picker class="time" v-model="startDate" type="date" placeholder="开始日期"
                        value-format="yyyy-MM-dd"></el-date-picker>
        至
        <el-date-picker class="time" v-model="endDate" type="date" placeholder="结束日期"
                        value-format="yyyy-MM-dd"></el-date-picker>
        <el-button class="filter-item search-btn" @click="getList">搜索</el-button>
      </div>
    </div>

    <!--列表-->
    <div class="system-list">
      <div class="item" v-for="(item,index) in listData" :key="index"
           @click="getDetailHandle(item.remindId)">
        <div class="left">
          <div
            :class="item.remindType === 0?'pic blue':item.remindType === 1?'pic orange':item.remindType === 3?'pic green':'pic'">
            {{item.remindType === 0?'待办消息':item.remindType === 1?'到期消息':item.remindType === 3?'提醒消息':''}}
            <div class="tip" v-if="item.isRead === 0"></div>
          </div>
        </div>
        <div class="right">
          <div class="title">
            <span @click.stop="titleHandle(item)"
                  :class="item.remindType === 0?'system-type blue':item.remindType === 1?'system-type orange':item.remindType === 3?'system-type green':'system-type'">
              {{item.remindType === 0?'待办消息':item.remindType === 1?'到期消息':item.remindType === 3?'提醒消息':''}}
            </span>
            {{item.companyName}}
          </div>
          <div class="text">
            {{item.remindContent}}
          </div>
        </div>
      </div>
      <div class="get-more-data">
        <span @click="getMore" v-if="isShowGetMore">点击加载更多</span>
        <div class="no-more" v-else>没有更多数据了</div>
      </div>
    </div>

    <!-- 查看 -->
    <el-dialog title="查看" class="editorUserModal" :visible.sync="sysVisible" width="600px" v-dialogDrag>
      <el-form ref="implementForm" label-width="120px">
        <el-form-item label="消息类型：">
          <span>
            {{sysModalData.remindType === 0?'待办消息':sysModalData.remindType === 1?'到期消息':sysModalData.remindType === 3?'提醒消息':''}}
          </span>
        </el-form-item>

        <el-form-item label="消息内容：">
          <span>您的客户：{{sysModalData.remindContent}}</span>
        </el-form-item>

        <el-form-item label="提醒时间：">
          <span>{{sysModalData.sendTime}}</span>
        </el-form-item>

      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button class="cananl" @click="sysVisible = false">取 消</el-button>
      </span>
    </el-dialog>
  </el-card>
</template>

<script>
  import {queryMsgRemindList, updateMsgRemindMarkRead, queryMsgRemindById} from '@/api/messageCenter'

  export default {
    data() {
      return {
        companyId: this.$store.getters.companyId,
        startDate: "",
        endDate: "",

        //当前类型
        remindType: 0,
        //全部总数量
        allNumber: "",
        //未读总数量
        noReadNumber: "",
        //列表数据
        listData: [],
        //tab栏数据列表数值
        remindCount: {
          upcoming: 0,
          maturity: 0,
          remind: 0
        },
        //弹窗
        sysVisible: false,
        sysModalData: {},
        page: 0,
        pageSize: 20,
        isShowGetMore: true,
      }
    },
    mounted() {
      this.getList()
    },
    methods: {
      //查询提醒列表
      getList() {
        let params = {
          companyId: this.companyId,
          remindType: this.remindType,
          markRead: false,
          startDate: this.startDate,
          endDate: this.endDate,
          page: this.page,
          pageSize: this.pageSize
        };
        queryMsgRemindList(params).then(res => {
          if (res.code === 200) {
            this.listData = res.data.remindList;
            if (res.data.remindCount) {
              this.remindCount = res.data.remindCount;
              this.allNumber = Number(res.data.remindCount.upcoming) + Number(res.data.remindCount.maturity) + Number(res.data.remindCount.remind);

              switch (this.remindType) {
                case 1:
                  this.noReadNumber = Number(res.data.remindCount.upcomingUnread);
                  break;
                case 2:
                  this.noReadNumber = Number(res.data.remindCount.maturityUnread);
                  break;
                case 3:
                  this.noReadNumber = Number(res.data.remindCount.remindUnread);
                  break;
                default:
                  this.noReadNumber = Number(res.data.remindCount.upcomingUnread) + Number(res.data.remindCount.maturityUnread) + Number(res.data.remindCount.remindUnread);
              }
            }
          }
        })
      },
      tabHandle(index) {
        this.page = 0;
        this.pageSize = 20;
        this.isShowGetMore = true;
        this.remindType = index;
        this.getList();
      },

      titleHandle(data) {
        this.sysModalData = data;
        this.sysVisible = true;
      },
      readAllHandle() {
        let params = {
          companyId: this.companyId,
          remindType: this.remindType,
          markRead: true,
          startDate: this.startDate,
          endDate: this.endDate,
          page: this.page,
          pageSize: this.pageSize
        };
        updateMsgRemindMarkRead(params).then(res => {
          if (res.code === 200) {
            this.getList();
          }
        })
      },

      //点击跳转到详情  合同:1; 订单:2; 专利:3; 商标:4,评论:5
      getDetailHandle(id) {
        queryMsgRemindById({remindId: id}).then(res => {
          if (res.code === 200) {
            this.listData.forEach(item => {
              if (item.remindId === id) {
                item.isRead = 1;
              }
            });

            switch (res.data.relType) {
              case 1:
                this.$router.push({
                  path: '/contract/checkContract',
                  query: {contractId: res.data.relId, isCheck: true}
                });
                break;
              case 2:
                this.$router.push({
                  path: '/declareCoordination/orderManage/orderDetail',
                  query:{orderId: res.data.relId, isCheck: true}
                })
                break;
              case 3:
                this.$router.push({
                  'path': '/ipr/patent/form/detail',
                  'query': {'type': 'read', 'patentId': res.data.relId}
                });
                break;
              case 4:
                this.$router.push({
                  'path': '/ipr/trademark/form/detail',
                  'query': {'type': 'read', 'trademarkId': res.data.relId}
                })
                break;
              case 5:
                this.$router.push({
                  path: '/declareCoordination/orderManage/orderDetail',
                  query:{orderId: res.data.relId, isCheck: true}
                })
                break;
            }
          }
        })
      },


      getMore() {
        this.page += 20;
        this.pageSize += 20;
        let params = {
          companyId: this.companyId,
          remindType: this.remindType,
          markRead: true,
          startDate: this.startDate,
          endDate: this.endDate,
          page: this.page,
          pageSize: this.pageSize
        };
        queryMsgRemindList(params).then(res => {
          if (res.code === 200) {
            if (res.data.remindList.length === 0) {
              this.isShowGetMore = false;
              return false;
            } else {
              this.listData = this.listData.concat(res.data.remindList);
            }
          }
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .system-card {
    border: 0;

    /deep/ .el-card__body {
      padding: 0;
    }

    .system-nav-bar {
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid #DBE9FF;
      padding: 10px 20px;

      .left {
        display: flex;
        align-items: center;

        div {
          height: 30px;
          line-height: 30px;
          margin-right: 14px;
          border: 1px solid;
          font-size: 16px;
          text-align: center;
          padding: 0 10px;
          border-radius: 2px;
          cursor: pointer;
          white-space: nowrap;
        }

        .line-center {
          color: rgba(68, 68, 68, 0.2980392156862745);;
          margin-right: 14px;
        }

        .all {
          border-color: #05AAFF;
          color: #05AAFF;

          &.cur {
            background: #05AAFF;
            color: #fff;
          }
        }

        .unread {
          border-color: #F56C6C;
          color: #F56C6C;

          &.cur {
            background: #F56C6C;
            color: #fff;
          }
        }

        .done {
          border-color: #05AAFF;
          color: #05AAFF;

          &.cur {
            background: #05AAFF;
            color: #fff;
          }
        }

        .expire {
          border-color: #FFA96A;
          color: #FFA96A;

          &.cur {
            background: #FFA96A;
            color: #fff;
          }
        }

        .warn {
          border-color: #48D462;
          color: #48D462;

          &.cur {
            background: #48D462;
            color: #fff;
          }
        }
      }

      .right {
        display: flex;
        align-items: center;
        .time {
          width: 140px;
          border: 0;
          border-bottom: 1px solid rgba(68, 68, 68, 0.2980392156862745);

          /deep/ .el-input__inner {
            border: 0;
            padding-right: 0;
          }
        }

        .search-btn {
          width: 60px;
          height: 30px;
          padding: 0;
          border-radius: 4px;
          color: #fff;
          border: 0;
          background: linear-gradient(180deg, rgba(105, 204, 255, 1) 0%, rgba(5, 170, 255, 1) 100%);
        }
      }
    }

    .system-list {
      overflow-y: auto;

      .item {
        display: flex;
        align-items: center;
        border-bottom: 1px solid rgba(68, 68, 68, 0.14901960784313725);
        padding: 20px;
        cursor: pointer;

        .left {
          margin-right: 30px;

          .pic {
            min-width: 70px;
            width: 70px;
            height: 70px;
            border-radius: 50%;
            color: #fff;
            position: relative;
            font-size: 14px;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 15px;
            text-align: center;

            .tip {
              width: 12px;
              height: 12px;
              background: #F56C6C;
              position: absolute;
              right: 5px;
              top: 2px;
              border-radius: 50%;
            }

            &.blue {
              background: #05AAFF;
            }

            &.orange {
              background: #FFA96A;
            }

            &.green {
              background: #48D462;
            }
          }
        }

        .right {
          font-size: 14px;

          .title {
            color: rgba(68, 68, 68, 0.75);
            margin-bottom: 14px;

            .system-type {
              margin-right: 14px;

              &.blue {
                color: #05AAFF;
              }

              &.orange {
                color: #FFA96A;
              }

              &.green {
                color: #48D462;
              }
            }

          }

          .text {
            color: #444;
          }
        }
      }

      .get-more-data {
        padding: 20px 0;
        text-align: center;

        .no-more {
          color: #ccc;
        }

        span {
          cursor: pointer;
        }
      }
    }

    .editorUserModal {
      /deep/ .el-dialog__header {
        border-bottom: 1px solid rgba(68, 68, 68, 0.3);
        padding: 20px 20px 16px;
      }

      /deep/ .el-dialog__title {
        line-height: 0;
      }

      /deep/ .el-dialog__headerbtn {
        top: 19px;
      }

      /deep/ .el-dialog__body {
        padding: 14px;
      }

      /deep/ .el-form-item__label {
        font-weight: normal;
        color: rgba(68, 68, 68, 0.75);
      }

      /deep/ .el-form-item__content {
        width: 73%;
        display: flex;
      }

      .user-input {
        /deep/ .el-input__inner {
          border: 0;
          border-radius: 0;
          border-bottom: 1px solid rgba(68, 68, 68, 0.3);
        }

        &.input {
          width: 100%;
          border-radius: 0;
          border: 0;
          border-bottom: 1px solid rgba(68, 68, 68, 0.3);

          &:focus {
            border: 0;
            border-bottom: 1px solid rgba(68, 68, 68, 0.3);
          }
        }
      }

      .user-select {
        width: 100%;
      }

      .switch-btn {
        font-size: 14px;
        color: #fff;
        background: rgba(5, 170, 255, 1);
        margin-left: 10px;

        &:active {
          opacity: .5;
        }
      }

      .modal-radio {
        display: flex;
        align-items: center;

        /deep/ .el-form-item__content {
          margin-left: 0 !important;
        }
      }

      .dialog-footer {
        .setBtn {
          color: #fff;
          background: rgba(5, 170, 255, 1);
        }

        .cananl {
          background: #fff;
          color: rgba(68, 68, 68, 1);
        }
      }
    }

    .noData {
      text-align: center;
      color: #909399;
      height: 50px;
      line-height: 50px;
      font-size: 14px;
    }
  }
</style>



